@charset "UTF-8";

@include media-breakpoint-down(md) {
	#storms-wc-searchbar-form {
		.categories-dropdown-btn {
			display: none;
		}
	}
}

ul.parent-list,
ul.child-list {
	padding: 0.25rem 1.5rem !important;

	.dropdown-item {
		&:hover,
		&:focus {
			background-color: transparent;
		}

		a {
			color: #222222;
		}
	}

}

.hidden {
	display: none;
}
.storms-wc-searchbar-loader-image {
	/* display: none; */
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	height: 25px;
	right: 50px;
	z-index: 9999;
	width: 25px;
	background-repeat: no-repeat;
	background-size: contain;
}
.storms-wc-searchbar-results {
	display: grid;
	gap: 0.25rem;
	margin: 10px 0;
}
.storms-wc-searchbar-result {
	border: 1px solid black;
	display: grid;
	grid-template-columns: 70px 1fr;
	gap: 0.25rem;
	padding: 10px;
}
.storms-wc-searchbar-result-image {
	text-align: center;
}
.storms-wc-searchbar-result-text h4,
.storms-wc-searchbar-result-text p {
	margin-bottom: 0;
	padding-bottom: 0;
}
